*
{
	margin: 0;
	padding: 0;
	box-sizing: border-box;;
}

body
{
	font-family: 'montseratt', sans-serif;
}

.App
{
	background-image: url('./assets/fog.jpg');
	background-size: cover;
	background-position: bottom;
	transition: 0.4s ease;
}

.App.rainy
{
  background-image: url("./assets/densy.jpg");
}
main
{
	min-height: 100vh;
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.50));
    padding: 25px;
}

.search-box
{
	width: 100%;
	margin: 0 0 75px;
}

.search-box .search-bar
{
	display: block;
	width: 100%;
	padding: 15px;

	appearance: none;
	background: none;
	border: none;
	outline: none;

    background-color: rgba(255,255,255,0.5);
    border-radius: 0px 0px 16px 16px;
    margin-top: -25px;

    box-shadow: 0px 5px rgba(0,0,0,0.2);

    color: #313131;
    font-size: 20px;

    transition: 0.4s ease;
}

.search-box .search-bar:focus
{
  background-color: rgba(255,255,255,0.50);
}

.location-box .location{
	color: skyblue;
	font-size: 45px;
	font-weight: 700;
	text-align: center;
	text-shadow: 4px 4px rgba(50,50,70,0.5);
}

.location-box .date
{
	color: yellow;
	font-size: 30px;
	font-weight: 200;
	font-style: italic;
	text-align: center;
	text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.weather-box
{
	text-align: center;
}

.weather-box .temp
{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: green;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather
{
	color:white;
	font-size: 48px;
	font-weight: 700;
	text-shadow: 3px 3px rgba(50,50,70,0.5);
}